import React from 'react'
import PictureModule from './unused/Selfie';

const Snap = () => {
  const [openSnap, setOpenSnap] = React.useState(false);
  const closeSnap = () => {
    setOpenSnap(false);
  }
  return (
   <>
    <section
        id="snap"
        className="section 2xl:px-[10%] px-[5%] lg:py-[15%] py-28 grid  items-center relative z-10"
      >
         
        <div className="flex justify-center flex-col sm:items-start items-center">
          <h2 className="smalltext sm:text-left text-center ">Snap, Smile and</h2>
          <h3 className="bigtext sm:text-left text-center">
            share your <br /> Moment
          </h3>
          <button onClick={() => setOpenSnap(true)} className="uppercase flex gap-2 2xl:text-lg lg:mt-[5%] mt-7 text-base items-center bg-white text-[#000A32] rounded-3xl py-1 px-3">
            <img className="h-6 w-4" src="/snap.svg" alt="img snap" />
            snap
          </button>
        </div>
        <div className="frame_img flex-1 relative 2xl:h-[500px] xl:h-[400px] h-[340px] sm:translate-x-[5%]  sm:block flex sm:items-start items-center sm:justify-normal justify-center">
          <div className="absolute top-[50%] -rotate-12 sm:left-[auto] left-[-10%] 2xl:w-[300px] xl:w-[270px] w-[180px] aspect-[9/16] z-0 opacity-0 image_1">
            <img
              src="/filter1.jpeg"
              alt="img frame"
              className="h-full w-full pointer-events-none object-cover select-none"
            />
          </div>
          <div className="absolute top-[50%] sm:left-[35%] 2xl:w-[300px] xl:w-[270px] w-[180px] aspect-[9/16] z-10 opacity-0 image_2">
            <img
              src="/filter2.jpeg"
              alt="img frame"
              className="h-full w-full pointer-events-none object-cover select-none"
            />
          </div>
          <div className="absolute top-[50%] sm:left-[65%] right-[-10%] rotate-12 2xl:w-[300px] xl:w-[270px] w-[180px] aspect-[9/16] opacity-0 image_3">
            <img
              src="/filter3.jpeg"
              alt="img frame"
              className="h-full w-full pointer-events-none object-cover select-none"
            />
          </div>
          <img
            src="/white_ring_complete.png"
            alt="white snap images"
            className="absolute sm:h-[300px] h-[200px] sm:top-[80%] top-[60%] lg:left-[200px] sm:left-[25%] left-[25%] z-[-1] pointer-events-none photo_ke_niche custom-spin-anti"
          />
        </div>
      </section>
      {openSnap && (
        <div className='fixed h-screen w-screen inset-0 z-[9999]'>
          <PictureModule
          closeSnap={closeSnap}
        />
        </div>
      )}
   </>
  )
}

export default Snap