import React, { useEffect, useRef } from 'react'
import Hls from "hls.js";
const Hero = () => {
    const videoRef = useRef(null);
  useEffect(() => {
    const hls = new Hls({
      debug: true,
    });

    if (Hls.isSupported() && videoRef.current) {
      hls.loadSource(
        "https://d208dezl4hi3dy.cloudfront.net/out/v1/ChannelGrp_66570fd77bb17/Channel_678df7c225fc0/non_drm_hls_endpoint_1737357250/index.m3u8"
      );
      hls.attachMedia(videoRef.current);
      hls.on(Hls.Events.ERROR, (err) => {
        console.log(err);
      });
    } else {
      console.log("load");
    }
    return () => {
      // cleanup (when component destroyed or when useEffect runs twice on StrictMode)
      hls.destroy();
    };
  }, []);
  return (
    <section className="lg:px-[10%] px-[5%]  section relative z-10 ">
        <div className="mb-[-10%] relative heading_">
          <h1 className="smalltext text-center ">Experience the action at</h1>
          <h2 className="bigtext text-center ">
            The Conviviality Awards
            <br />
            3rd Edition 2025
          </h2>
        </div>
        <div className="w-full flex items-center justify-center">
          <div className="iframe_div relative">
            <img
              src="/small_ring.png"
              alt="ring around live video"
              className="w-[400px] h-[400px] absolute right-[-250px] top-[-100px] z-[-1] mix-blend-screen custom-spin iframe_right"
            />
            <img
              src="/stars.png"
              alt="ring around live video"
              className="w-[100vh] h-[100vh] absolute right-[-50vh] top-[0px] z-[-1] mix-blend-screen custom-spin "
            />
            <img
              src="/white_ring_complete.png"
              alt="ring around live video"
              className="min-w-[60vw] w-[60vw] min-h-[60vw] h-[60vw] absolute left-[-400px] bottom-[-300px] z-[-1] mix-blend-screen custom-spin iframe_right"
            />
            <img
              src="/stars.png"
              alt="ring around live video"
              className="w-[100vh] h-[100vh] absolute left-[-50vh] top-[5vh] z-[-1] mix-blend-screen custom-spin-anti  "
            />
            {/* <iframe
              width=""
              height=""
              src="https://www.youtube.com/embed/qgmqIpIbBVY?autoplay=0&mute=1"
              title="YouTube video player"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
              referrerPolicy="strict-origin-when-cross-origin"
              allowFullScreen
            ></iframe> */}
            <video
              ref={videoRef}
              controls
              style={{ width: "100%", height: "100%", borderRadius: "0" }}
            />
          </div>
        </div>
        <div className="emoji-list border bg-[#0000004D] p-2 rounded-[30px] w-fit relative left-[50%] translate-x-[-50%] translate-y-[-80%]">
          <span className="emoji border rounded-full" data-emoji="üëç">
            üëç
          </span>
          <span className="emoji border rounded-full" data-emoji="‚ù§Ô∏è">
            ‚ù§Ô∏è
          </span>
          <span className="emoji border rounded-full" data-emoji="üéâ">
            üéâ
          </span>
        </div>
      </section>
  )
}

export default Hero