
import React, { useEffect, useState } from 'react';
import $ from 'jquery';
import { increment, ref, update } from 'firebase/database';
import { database } from '@/config/config';
// import { FaDownload, FaTimes } from 'react-icons/fa';

const PictureModule = ({ closeSnap }) => {
  const [showPopup, setShowPopup] = useState(false);
  const frameList = [
    'Filter1.png',
    'Filter2.png',
    'Filter3.png',
  ];

  const changeFrame = (e) => {
    const imageUrl = e.target.dataset.url;
    $('.overlay-frame').css('backgroundImage', `url('${imageUrl}')`);
  };


  const saveSelfieCount = async () => {
    try {
      const optionRef = ref(database, `selfie`);
      await update(optionRef, {
        capture: increment(1),
      });
      console.log("Poll submission result updated in Realtime Database.");
    } catch (error) {
      console.error(
        "Error updating poll submission result in Realtime Database: ",
        error
      );
    }
  
  };

  useEffect(() => {
    const video = document.getElementById('camera');
    const canvas = document.getElementById('snapshot');
    const output = document.getElementById('output');
    const image_download_popup = document.getElementById('image_download_popup');
    const overlayFrame = document.querySelector('.overlay-frame');

    // Access the device camera
    navigator.mediaDevices.getUserMedia({ video: true })
      .then((stream) => {
        video.srcObject = stream;
        console.log('Stream:', stream);
      })
      .catch((err) => {
        console.error('Error accessing the camera:', err);
      });

    // Capture the image
    document.getElementById('capture').addEventListener('click', () => {
      const context = canvas.getContext('2d');
      let aspectRatio = 16 / 9;
      let targetWidth = 1920;
      let targetHeight = 1080;

      if (window.innerWidth < 768) {
        aspectRatio = 9 / 16;
        targetWidth = 1080;
        targetHeight = 1920;
      }

      const videoWidth = video.videoWidth;
      const videoHeight = video.videoHeight;

      let cropWidth, cropHeight, offsetX, offsetY;

      if (videoWidth / videoHeight > aspectRatio) {
        cropHeight = videoHeight;
        cropWidth = cropHeight * aspectRatio;
        offsetX = (videoWidth - cropWidth) / 2;
        offsetY = 0;
      } else {
        cropWidth = videoWidth;
        cropHeight = cropWidth / aspectRatio;
        offsetX = 0;
        offsetY = (videoHeight - cropHeight) / 2;
      }

      canvas.width = targetWidth;
      canvas.height = targetHeight;

      // Flip the canvas horizontally to correct the mirror effect
      context.translate(canvas.width, 0);
      context.scale(-1, 1);

      context.drawImage(
        video,
        offsetX,
        offsetY,
        cropWidth,
        cropHeight,
        0,
        0,
        targetWidth,
        targetHeight
      );

      // Reset transformations
      context.setTransform(1, 0, 0, 1, 0, 0);

      // Draw the selected overlay frame
      const overlayImage = new Image();
      overlayImage.src = window.getComputedStyle(overlayFrame).backgroundImage.slice(5, -2);
      overlayImage.onload = function () {
        context.drawImage(overlayImage, 0, 0, canvas.width, canvas.height);

        // Show the captured image with the frame
        const dataUrl = canvas.toDataURL('image/png');
        output.src = dataUrl;
        document.querySelector('.out_link').href = dataUrl;
        output.style.display = 'block';
        image_download_popup.style.display = 'flex';
      };
    });
  }, []);
  useEffect(() => {
    setTimeout(() => {
      const canvas = document.getElementById('snapshot');
      if (!canvas) {
        console.error('Canvas not found');
        return;
      }
      const context = canvas.getContext('2d');
      console.log('Canvas ready:', context);
    }, 500);
  }, []);
  return (
    <div id="app" className="relative w-full h-screen mx-auto">
      <button onClick={closeSnap} className='absolute top-4 right-4 bg-white text-[#000A32] py-2 px-6 rounded-3xl shadow-lg z-30'>Close</button>
      <div id="frame-container" className="relative w-full h-screen overflow-hidden">
        <video id="camera" autoPlay playsInline className="w-full h-screen"></video>
        <div 
          className="overlay-frame absolute top-0 left-0 w-full h-full pointer-events-none"
        ></div>
      </div>

      {/* Frame selection & Capture Button */}
      <div className="bg-black/50 fixed bottom-0 w-screen h-auto">
        <div className="w-full overflow-x-auto overflow-y-hidden h-28 flex gap-6 items-center justify-center">
          {frameList.map((frame, index) => (
            <div key={index} className="cursor-pointer min-w-28 bg-white">
              <img
                onClick={changeFrame}
                src={frame}
                alt={`Frame ${index + 1}`}
                width={100}
                height={100}
                data-url={`/${frame}`}
              />
            </div>
          ))}
        </div>
        <div className="flex justify-center my-4">
          <button 
            id="capture" 
            className="bg-white text-[#000A32] py-2 px-6 rounded-3xl shadow-lg"
          >
            Capture
          </button>
        </div>
      </div>

      {/* Captured Image Popup */}
      {/* {showPopup && ( */}
        <div id='image_download_popup' onClick={(e)=>{
         
          const link = document.querySelector('#image_download_popup');
          link.style.display = 'none';
        }} className="fixed inset-0 bg-black/80 flex items-center justify-center z-50">
          <div onClick={(e) => e.stopPropagation()} className="relative bg-white p-6 rounded-lg shadow-lg max-w-md mx-auto">
            <button
              onClick={() => setShowPopup(false)}
              className="absolute top-2 right-2 text-gray-600 text-2xl"
            >
              {/* <FaTimes /> */}
            </button>
            <canvas id="snapshot" className="hidden"></canvas>
            <img id="output" alt="Captured" className="max-w-full rounded-lg" />
            <div className="flex justify-center mt-4">
              <a
                className="out_link text-white bg-blue-500 px-6 py-3 rounded-md flex items-center gap-2"
                download
                href=""
                target="_blank"
                onClick={(e) => {
                  
                  const link = document.querySelector('#image_download_popup');
                  link.style.display = 'none';
                  saveSelfieCount();
                }}
                rel="noopener noreferrer"
              >
                {/* <FaDownload /> */}
                Download
              </a>
            </div>
          </div>
        </div>
      {/* )} */}
    </div>
  );
};

export default PictureModule;
