import React from 'react'

const Header = ({pollIndexNumber}) => {
      function clearUserAndReload() {
        localStorage.removeItem("user");
        window.location.href = window.location.origin + window.location.pathname;
    }
  return (
    <header className=" flex justify-between 2xl:10% px-[5%] py-7 header">
            <div>
              <img src="/logo.png" alt="logo home" className=" 2xl:h-20 h-14" />
            </div>
            <div className="flex items-center xl:gap-10 gap-5">
              <a
                href=""
                className="text-[#D9D9C4] 2xl:text-lg text-base opacity-90"
              >
                Live Feed
              </a>
              <a
                href=""
                className="text-[#D9D9C4] 2xl:text-lg text-base opacity-90"
              >
                Snap Station
              </a>
              <a
                href=""
                className="text-[#D9D9C4] 2xl:text-lg text-base opacity-90"
              >
                Trivia Time
              </a>
              <a
                href=""
                className="text-[#D9D9C4] 2xl:text-lg text-base opacity-90"
              >
                Get In Touch
              </a>
              <a
                href="#poll"
                className="text-[#FF4444] border px-2 pr-4 border-red-500 py-1 rounded-3xl 2xl:text-lg text-base opacity-90 flex gap-2 items-center"
              > {pollIndexNumber &&  <span className="bg-red-500 animate-pulse text-black rounded-full w-6 h-6 grid place-items-center">{pollIndexNumber} </span> }
                Live Polling
              </a>
              
              <button
                onClick={clearUserAndReload}
                className="uppercase flex gap-2 2xl:text-lg text-base items-center bg-white text-[#000A32] rounded-3xl py-1 px-3"
              >
                <img className="h-6 w-4" src="/logout.svg" alt="logout svh" />{" "}
                logout
              </button>
            </div>
          </header>
  )
}

export default Header